import React from 'react';
import TitleComponent from './TitleComponent';
import '../styles/laurelsSection.css'
import laurel_1 from '../images/laurel_1.png'
import laurel_2 from '../images/laurel_2.png'
import laurel_3 from '../images/laurel_3.png'
import laurel_4 from '../images/laurel_4.png'
import laurels__image from '../images/laurel__image.png';
import laurels__G from '../images/laurels__G.png';
import { motion } from 'framer-motion';
import { fadeIn } from '../utils/motion';
import laurels__stamp from '../images/laurels__stamp.png'

const laurelsContent = [
  {
  title: 'Bib Gourmond',
  description: 'Lorem ipsum dolor sit amet, consectetur.',
  image: laurel_1
  },
  {
    title: 'Rising Star',
    description: 'Lorem ipsum dolor sit amet, consectetur.',
    image: laurel_2
  },
  {
    title: 'AA Hospitality ',
    description: 'Lorem ipsum dolor sit amet, consectetur.',
    image: laurel_3
  },
  {
    title: 'Outstanding Chef',
    description: 'Lorem ipsum dolor sit amet, consectetur.',
    image: laurel_4
  },
]

const LaurelsItem = ({ title, description, image }) => (
  <div className='laurels__item'>
    <img className='laurels__item--image' src={image} alt="laurel"/>
    <div className="laurels__item--wrapper">
      <div className="laurels__item--title heading">{title}</div>
      <div className="laurels__item--description description">{description}</div>
    </div>
  </div>
)

const LaurelsSection = () => {
  return (
    <section className='section__laurels sectionPaddingY'>
      <div className="containerSecondary laurels-container">
        <motion.div
          initial="hidden"
          whileInView="show"
          viewport={{ once: false, amount: 0.25 }}
          className="laurels"
        >
          <motion.div
            className="laurels__text"
            variants={fadeIn('right', 'tween', 0.2, 1)}
          >
            <TitleComponent
              titleSmall
              subtitle='Awards & recognition'
              title='Our Laurels'
            />
            <div className="laurels__list">
              {laurelsContent.map(({ title, description, image }) =>
                <LaurelsItem key={title} title={title} description={description} image={image} />
              )}
            </div>
          </motion.div>
          <motion.div
            className="laurels__image"
            variants={fadeIn('left', 'tween', 0.2, 1)}
          >
            <img src={laurels__image} alt="meal"/>
            <img className='laurels__image--g' src={laurels__G} alt="g"/>
          </motion.div>
        </motion.div>
      </div>
      <img className='laurels__stamp' src={laurels__stamp} alt="stamp"/>
    </section>
  );
};

export default LaurelsSection;
