import React from 'react';
import '../styles/heroSection.css';
import TitleComponent from './TitleComponent';
import { Swiper, SwiperSlide } from 'swiper/react'
import { Pagination } from 'swiper';
import 'swiper/css';
import { motion } from 'framer-motion';
import { fadeIn, staggerContainer } from '../utils/motion';
import slide_1 from '../images/slide1.png'
import slide_2 from '../images/slide2.png'
import slide_3 from '../images/slide3.png'

const HeroSection = () => {

  const pagination = {
    clickable: true,
    bulletClass: 'sliderBtn',
    bulletActiveClass: 'active',
    renderBullet: function (index, className) {
      return  '<span class="' + className + '">'
        + '0' + (index + 1) +
        '<span>' + ' - ' + '</span>' +
        "</span>";
    },
  };

  return (
    <section className='section__hero'>
      <div className="container hero__container">
        <motion.div
          className="hero"
          variants={staggerContainer}
          initial="hidden"
          whileInView="show"
          viewport={{ once: false, amount: 0.25 }}
        >
          <motion.div
            className="hero__text"
            variants={fadeIn('right', 'tween', 0.2, 1)}
          >
            <TitleComponent
              subtitle="Chase the new Flavour"
              title="The key to Fine dining"
            />
            <div className="hero__description description">
              Sit tellus lobortis sed senectus vivamus molestie. Condimentum volutpat morbi facilisis quam scelerisque sapien. Et, penatibus aliquam amet tellus
            </div>
            <button className="hero__button button hvr-sweep-to-right">Explore Menu</button>
          </motion.div>
          <motion.div
            className="hero__slider"
            variants={fadeIn('left', 'tween', 0.2, 1)}
          >
            <Swiper
              spaceBetween={50}
              slidesPerView={1}
              pagination={pagination}
              modules={[Pagination]}
            >
              <SwiperSlide className="hero__slide">
                <img src={slide_1} alt="slide"/>
              </SwiperSlide>
              <SwiperSlide className="hero__slide">
                <img src={slide_2} alt="slide"/>
              </SwiperSlide>
              <SwiperSlide className="hero__slide">
                <img src={slide_3} alt="slide"/>
              </SwiperSlide>
            </Swiper>
          </motion.div>
        </motion.div>
      </div>
    </section>
  );
};

export default HeroSection;
