import React from 'react';
import TitleComponent from './TitleComponent';
import contact__image from '../images/contact__image.png'
import '../styles/contactSection.css'
import { motion } from 'framer-motion';
import { fadeIn } from '../utils/motion';

const ContactSection = () => {
  return (
    <section className='section__contact sectionPaddingY'>
      <motion.div
        className='containerSecondary contact__container'
        initial="hidden"
        whileInView="show"
        viewport={{ once: false, amount: 0.25 }}
      >
        <motion.div
          className="contact__text"
          variants={fadeIn('right', 'tween', 0.2, 1)}
        >
          <TitleComponent
            titleSmall
            subtitle='Contact'
            title='Find Us'
          />
          <div className="contact__text--description description">
            Lane Ends Bungalow, Whatcroft Hall Lane, Rudheath, CW9 7SG
          </div>
          <div className="contact__text--hours">
            <h4 className='heading hours__title'>Opening Hours</h4>
            <div className="hours__detail">
              Mon - Fri: 10:00 am - 02:00 am <br />
              Sat - Sun: 10:00 am - 03:00 am
            </div>
            <button className="hours__button button">Visit Us</button>
          </div>
        </motion.div>
        <motion.div
          className="contact__image"
          variants={fadeIn('left', 'tween', 0.2, 1)}
        >
          <img src={contact__image} alt="contact image"/>
        </motion.div>
      </motion.div>
    </section>
  );
};

export default ContactSection;
