import React, { useState } from 'react';
import '../styles/header.css';

const navLinks = ['Home', 'Pages', 'Contact Us', 'Blog', 'Landing'];

const Header = () => {
  const [menuOpen, setMenuOpen] = useState(false);
  const body = document.getElementById('body');

  const handleClick = () => {
    setMenuOpen(!menuOpen)
    !menuOpen ? body.classList.add('lock') : body.classList.remove('lock')
  }

  const handleLinkClick = () => {
    setMenuOpen(false);
    if (body.classList.contains('lock')) {
     body.classList.remove('lock')
    }
  }

  return (
    <header className="header">
      <a className="logo" href="/">
        Gericht
      </a>
      <nav className={`nav ${menuOpen ? 'open' : ''}`}>
        {navLinks.map(link =>
          <a
            onClick={handleLinkClick}
            className="nav__link" href='#' key={link}>
            {link}
          </a>)}
      </nav>
      <div className="controls">
        <button>Log in /<br /> registration</button>
        <div className='divider' />
        <button>book <br /> table</button>
      </div>
      <button onClick={handleClick} className='burger'>
        <div />
        <div />
        <div />
      </button>
    </header>
  );
};

export default Header;
