import React from 'react';
import '../styles/aboutSection.css';
import { motion } from 'framer-motion';
import { fadeIn, slideIn, staggerContainer } from '../utils/motion';
import knife from '../images/knife.png'

const AboutSection = () => {

  return (
    <section className='section__about sectionPaddingY'>
     <motion.div
       className="about"
       variants={staggerContainer}
       initial="hidden"
       whileInView="show"
       viewport={{ once: false, amount: 0.25 }}
     >
       <motion.div
         className="aboutUs"
         variants={fadeIn('right', 'tween', 0.2, 1)}
       >
         <h2 className="about__title title">About Us</h2>
         <svg width="40" height="10" viewBox="0 0 40 10" fill="none" xmlns="http://www.w3.org/2000/svg">
           <g clipPath="url(#clip0_0_37)">
             <path d="M33.5014 9.5C37.0847 9.5 39.9998 7.48129 39.9998 4.99995C39.9998 2.51861 37.0847 0.5 33.5014 0.5C30.112 0.5 27.3219 2.30637 27.0298 4.6018H22.3527V3.6552L-0.000198364 3.6552V6.34446L22.3528 6.34446V5.39798H27.03C27.3221 7.69352 30.112 9.5 33.5014 9.5ZM21.4896 5.54839L0.863094 5.54839V4.45161H21.4896V5.54839ZM27.866 4.99995C27.866 2.95778 30.3942 1.2964 33.5015 1.2964C36.6089 1.2964 39.137 2.95778 39.137 4.99995C39.137 7.04222 36.6089 8.7037 33.5015 8.7037C30.3942 8.7037 27.866 7.04222 27.866 4.99995Z" fill="#DCCA87"/>
           </g>
           <defs>
             <clipPath id="clip0_0_37">
               <rect width="40" height="9" fill="white" transform="translate(-0.000198364 0.5)"/>
             </clipPath>
           </defs>
         </svg>
         <p className="about__description description">
           Lorem ipsum dolor sit amet, consectetur adipiscing elit. Quis pharetra adipiscing ultrices vulputate posuere tristique. In sed odio nec aliquet eu proin mauris et.
         </p>
         <button className="button about__button">
           Know More
         </button>
       </motion.div>
       <motion.div
         className="knife"
         variants={slideIn('up', 'tween', 0.4, 1)}
       >
         <img src={knife} alt="knife"/>
       </motion.div>
       <motion.div
         className="ourHistory"
         variants={fadeIn('left', 'tween', 0.2, 1)}
       >
         <h2 className="about__title title">Our History</h2>
         <svg width="40" height="10" viewBox="0 0 40 10" fill="none" xmlns="http://www.w3.org/2000/svg">
           <g clipPath="url(#clip0_0_37)">
             <path d="M33.5014 9.5C37.0847 9.5 39.9998 7.48129 39.9998 4.99995C39.9998 2.51861 37.0847 0.5 33.5014 0.5C30.112 0.5 27.3219 2.30637 27.0298 4.6018H22.3527V3.6552L-0.000198364 3.6552V6.34446L22.3528 6.34446V5.39798H27.03C27.3221 7.69352 30.112 9.5 33.5014 9.5ZM21.4896 5.54839L0.863094 5.54839V4.45161H21.4896V5.54839ZM27.866 4.99995C27.866 2.95778 30.3942 1.2964 33.5015 1.2964C36.6089 1.2964 39.137 2.95778 39.137 4.99995C39.137 7.04222 36.6089 8.7037 33.5015 8.7037C30.3942 8.7037 27.866 7.04222 27.866 4.99995Z" fill="#DCCA87"/>
           </g>
           <defs>
             <clipPath id="clip0_0_37">
               <rect width="40" height="9" fill="white" transform="translate(-0.000198364 0.5)"/>
             </clipPath>
           </defs>
         </svg>
         <p className="about__description description">
           Adipiscing tempus ullamcorper lobortis odio tellus arcu volutpat. Risus placerat morbi volutpat habitasse interdum mi aliquam In sed odio nec aliquet.
         </p>
         <button className="button about__button">
           Know More
         </button>
       </motion.div>
     </motion.div>
    </section>
  );
};

export default AboutSection;
