import React, { useEffect, useState } from 'react';
import TitleComponent from './TitleComponent';
import '../styles/subscribeSection.css'
import * as EmailValidator from 'email-validator';
import { motion } from 'framer-motion';
import { fadeIn } from '../utils/motion';
import { TypingText } from './CustomText';

const SubscribeSection = () => {
  const [inputValue, setInputValue] = useState('');
  const [btnDisabled, setBtnDisabled] = useState(true);
  const [formLoading, setFormLoading] = useState(false)

  const handleInputChange = (event) => {
    setInputValue(event.target.value)
  }

  const handleSubmit = (event) => {
    event.preventDefault()
    setFormLoading(true)
    setBtnDisabled(true)
    setTimeout(() => {
      setFormLoading(false)
      setBtnDisabled(false)
    }, 2000)
  }

  useEffect(() => {
    setBtnDisabled(!EmailValidator.validate(inputValue))
  }, [inputValue])

  return (
    <section className='section__subscribe'>
      <motion.div className="containerSecondary subscribe__container"
                  initial="hidden"
                  whileInView="show"
                  viewport={{ once: false, amount: 0.25 }}
      >
        <motion.div
          variants={fadeIn('right', 'tween', 0.2, 1)}
        >
          <TitleComponent
            titleSmall
            title='Subscribe to Our Newsletter'
            subtitle='Newsletter'
          />
        </motion.div>
        <TypingText
          title='And never miss latest Updates!'
          textStyles='subscribe__text'
        />
        <motion.form
          action="#"
          className="subscribe__form"
          onSubmit={handleSubmit}
          variants={fadeIn('left', 'tween', 0.2, 1)}
        >
          <input
            className='subscribe__input'
            type="text"
            placeholder='Email Address'
            value={inputValue}
            onChange={handleInputChange}
          />
          <button
            disabled={btnDisabled}
            className='subscribe__button button'
          >
            {formLoading
              ? <div className='spinner' />
              : 'Subscribe'}
          </button>
        </motion.form>
      </motion.div>
    </section>
  );
};

export default SubscribeSection;
