import React, { useRef, useState } from 'react';
import video from '../video/meal.mp4'
import '../styles/videoSection.css';
import { BsFillPlayFill, BsFillPauseFill } from 'react-icons/bs';

const Video = () => {
  const [playVideo, setPlayVideo] = useState(false);
  const videoRef = useRef();
  const handleClick = () => {
    setPlayVideo(prevState => !prevState);

    playVideo ? videoRef.current.pause() : videoRef.current.play()
  }

  return (
    <section className='video'>
      <video
        ref={videoRef}
        src={video}
        muted
        controls={false}
        loop
      />
      <div className="video__overlay">
        <div className="video__control" onClick={handleClick}>
          {!playVideo
            ? <BsFillPlayFill fill='#ffffff' size={30} />
            : <BsFillPauseFill fill='#ffffff' size={30} />
          }
        </div>
      </div>
    </section>
  );
};

export default Video;
