import React from 'react';
import '../styles/gallerySection.css';
import TitleComponent from './TitleComponent';
import gallery_1 from '../images/gallery_1.png';
import gallery_2 from '../images/gallery_2.png';
import gallery_3 from '../images/gallery_3.png';
import gallery_4 from '../images/gallery_4.png';
import { motion } from 'framer-motion';
import { fadeIn } from '../utils/motion';

const GallerySection = () => {
  return (
    <section className='section__gallery'>
      <motion.div
        className="gallery"
        initial="hidden"
        whileInView="show"
        viewport={{ once: false, amount: 0.25 }}
      >
        <motion.div
          className="gallery__text"
          variants={fadeIn('right', 'tween', 0.2, 1)}
        >
          <TitleComponent
            titleSmall
            title='Photo Gallery'
            subtitle='Instagram'
          />
          <div className="gallery__text--description description">
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Volutpat mattis ipsum turpis elit elit scelerisque egestas mu.
          </div>
          <button className="button gallery__text--btn">View More</button>
        </motion.div>
        <motion.div
          className="gallery__content"
          variants={fadeIn('left', 'tween', 0.2, 1)}
        >
          <div className="gallery__item">
            <img src={gallery_1} alt="meal"/>
          </div>
          <div className="gallery__item">
            <img src={gallery_2} alt="meal"/>
          </div>
          <div className="gallery__item">
            <img src={gallery_3} alt="meal"/>
          </div>
          <div className="gallery__item">
            <img src={gallery_4} alt="meal"/>
          </div>
        </motion.div>
      </motion.div>
    </section>
  );
};

export default GallerySection;
